</template>

<script>
export default {
  name: 'DroneDashboard',
  data() {
    return {
      sidebarCollapsed: false,
      selectedMenuItem: 'map',
      subMenuOpen: {
        ai: false
      },
      pageTitles: {
        map: '地图管理',
        field: '农田管理',
        task: '任务管理',
        data: '数据分析',
        drone: '设备管理',
        settings: '系统设置',
        logs: '系统日志',
        ai: 'AI智能分析',
        disease: '病虫害智能识别',
        field_analysis: '农田边界与长势分析'
      }
    }
  },
  mounted() {
    // 页面加载时记录当前选中的菜单项
    this.updateActiveMenuItem();
  },
  
  watch: {
    // 监听路由变化，更新选中的菜单项
    $route: {
      handler() {
        this.updateActiveMenuItem();
      },
      immediate: true
    }
  },
  computed: {
    currentPageTitle() {
      return this.pageTitles[this.selectedMenuItem] || '控制面板';
    }
  },
  methods: {
    toggleSidebar() {
      this.sidebarCollapsed = !this.sidebarCollapsed;
    },
    selectMenuItem(item) {
      this.selectedMenuItem = item;
    },
    toggleSubMenu(group) {
      this.subMenuOpen[group] = !this.subMenuOpen[group];
    },
    isActiveRoute(routePath) {
      return this.$route.path.startsWith(routePath);
    },
    logout() {
      // 退出登录逻辑
      this.$router.push('/');
    },
  
    // 根据当前路由更新活动菜单项
    updateActiveMenuItem() {
      const path = this.$route.path;
      
      // 根据路由路径确定当前选中的菜单项
      if (path.includes('disease')) {
        this.selectedMenuItem = 'disease';
        this.subMenuOpen.ai = true;
      } else if (path.includes('field') && path.includes('ai')) {
        this.selectedMenuItem = 'field_analysis';
        this.subMenuOpen.ai = true;
      } else if (path.includes('ai')) {
        this.selectedMenuItem = 'ai';
      } else {
        // 处理其他顶级菜单项
        Object.keys(this.pageTitles).forEach(key => {
          if (path.includes(key)) {
            this.selectedMenuItem = key;
          }
        });
      }
    }
  }
}
</script>

